.fieldset {
  display: grid;
}

.label {
  display: grid;
  margin: 15px 0px;

  text-transform: capitalize;
  font-weight: 700;
}

.input {
  width: 400px;
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-top: 15px;
  padding: 10px;
  width: 400px;
}

.btnText {
  display: contents;
  text-transform: capitalize;
}
